@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap");

* {
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}
.container{
  margin-top: 8%;
}

body {
  margin: 0;
  background-color: #0d1323;
  font-family: "Rubik", sans-serif;
}
.card {
  border-radius: 22px;
  margin-top: 10px;
  border: none;
}
.card_content {
  margin: 0;
}
.card_header {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border-radius: 17px;
  background-color: #5747ea;
  padding: 1.9rem 0rem 1.9rem 0rem;
}

.card_header img {
  width: 20%;
  border: 3px solid white;
  border-radius: 67px;
}

.card_header p {
  opacity: 0.5;
  color: white;
}

.card_header h1 {
  color: white;
  font-weight: 200;
}

.card-body {
  padding: 0;
  background: #1d1f49;
  border-radius: 18px;
}
.status {
  width: 70%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 0rem 1.5rem 0rem;
}
a {
  text-decoration: none;
  color: #bdc1ff;
}
.status .active {
  color: white;
}
.inactive{
  opacity: 0.5;
}

.work-body {
  background-color: #ff8c65;
}
.play-body {
  background-color: #55c2e6;
}
.study-body {
  background-color: #fe5b7c;
}
.exercise-body {
  background-color: #4bcf81;
}
.social-body {
  background-color: #7535d3;
}
.self-care-body {
  background-color: #f1c65a;
}
.work,
.play,
.study,
.exercise,
.social,
.self-care {
  padding: 1.3rem 0rem 1.3rem 0rem;
  position: relative;
  overflow: hidden;
}
svg {
  position: absolute;
  right: 5%;
  top: -18%;
}

.work_header {
  background-color: #1d1f49;
  transition: all .25s ease-in-out;
}
.work_header .row {
  width: 88%;
}
.eclipse {
  position: absolute;
  top: 20%;
  right: 10%;
}
.work_hrs h1 {
  font-size: 1.4rem;
}
.work_hrs p {
  margin: 0;
}
.work_week p {
  text-align: right;
  font-size: small;
  margin: 27px 0px 0px 0px;
}
.work_header:hover:not(.eclipse){
  background: #4f53b5;
  cursor: pointer;
}
a:hover{
  color: white;
}
@media (min-width: 1024px) {
  .head {
    display: block;
  }
  img {
    margin: 0% 0% 10% 10%;
    width: 30% !important;
  }
  .status {
    width: 80%;
    flex-direction: column;
    align-items: flex-start;
  }
  .status a {
    margin-top: 15px;
  }
  @media (min-width: 1200px) {
    .col-xl-7-5-c {
      width: 62%;
    }
    .work_hrs h1 {
      font-size: 2.3rem;
    }
    .card_content h1 {
      font-size: 3rem;
    }
    .card_content {
      margin-left: 2rem;
    }
    .card_header img {
      border-radius: 44px;
    }
  }
  @media (min-width:1400px){
    .col-xl-7-5-c {
      width: 50%;
    }
    .work_week p{
      margin: 0
    }
  }
}
